<template>
  <div class="body04">
    <div class="hospital-label">
      <span></span>
    </div>
    <div class="recommendhospitals">
      <ul  v-for="hospital in hospitalList" :key="hospital.itemid">
        <li class="item border-bottom">
          <!---->
          <div class="item-img-wrapper" style="margin: auto" >
      <!--  <img class="item-img" src="http://localhost/gyt/images/hospital.png">-->
            <img class="item-img" :src="hospital.natmehaFileDTO.filePath">
          </div>

          <div class="item-info">
      <!--  <p class="item-title"></p>-->
            <!---->
            <p class="item-title">{{hospital.hospitalName}}</p>
            <p class="item-desc">
              <!---->
              <el-rate v-model="hospital.hospitalSource" disabled></el-rate>
            </p>
            <!---->
            <p class="item-price">
               {{hospital.hospitalBooking}}
              <span class="item-location"></span>
            </p>
          </div>
        </li>
      </ul>
    </div>
</div>
</template>

<script>
  import { mapGetters, mapActions } from 'vuex'

  export default {
    name: "FinishHospitalBody",
    components: {

    },
    data(){
      return{
        options: {timeout: 9000},
        position: {
          city:"",
          province:""
        }//
      }
    },
    methods:{
      ...mapActions(['getHospitalList']),

      //
      getMyLocation() {
        var geolocation = new qq.maps.Geolocation("2WABZ-2VCL5-ZQWIB-QVBUE-AFWRK-5DBZE","gyt");
        geolocation.getLocation(this.setPosition, this.showErr,this.options);
      },
     setPosition(position) {//
        console.log(position);
        this.$data.position = position;

      },
      showErr() {
        console.log("");
        //this.getMyLocation();//
      },

    },
    mounted(){
       this.getMyLocation();
       this.getHospitalList(this.position);
    },
    computed: {
      //  Vuex  getter  scenicSpotDetail 
      ...mapGetters(['hospitalList'])
    },


  }
</script>

<style lang="stylus" scoped>
.body04
  display flex
  flex-direction column
  .step
    margin-top 7px
    margin-bottom 7px
  .recommendhospitals
    .item
      margin auto
      overflow hidden
      display flex
      height 115px
      width 350px
      .item-img
        width 70px
        height 70px
        padding 15px
      .item-info
        margin-right 50px
        text-align left
        flex 1
        padding 15px
        min-width 0
        font-size 14px
        .item-title
          line-height 27px
          font-weight bold
          font-size 16px
          font-family Arial
          color #d98d2a
          overflow hidden
          white-space nowrap
          text-overflow ellipsis
        .item-desc
          line-height 20px
        .item-price
          line-height 22px
          margin-top 10px
          color #aaa
          font-size 14px
          .item-location
            display inline-block
            float right
            right 20px
            bottom 5px
            color #616161
            font-size 12px
  .hospital-label
    font-size large
    font-weight bold
    color #775637
    text-align left
    margin-left 5px
</style>



