<template>

  <div class="CustToast"
       :class="type"
       v-if="showToast">
    {{ message }}
  </div>

</template>

<script>
export default {
  /**
   * Toast v0.2
   * params: showToast Boolean toast  false
   * params: type String       toast normal successfailwarning  normal
   * params: message String    toast
   * params: duration Number      toast  3000ms
   * */
  name: "CustToast",
  data() {
    return {
      showToast: true,
      type: "normal",
      message: "",
      duration: 3000
    }
  },
}
</script>

<style scoped>
.CustToast {
  position: fixed;
  left: 50%;
  top: 50%;
  background: rgba(0, 0, 0, 0.57);
  padding: 10px;
  border-radius: 5px;
  transform: translate(-50%, -50%);
  animation: show-toast .2s;
  color: aliceblue;
  overflow: hidden;
  display: flex;
  align-items: center;
}

@keyframes show-toast {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
</style>



