<template>
      <div class="addpatienbody"  >
      <div  style="height:39px   ;background: #775637;text-align: center">
        <router-link to="/patienmanage" >
          <span class="icon iconfont youjiantou" style="float: left">&#xe624;</span>
        </router-link>
        <span style="height: 39px;line-height: 39px;color: white;font-size: 16px"></span>
      </div>
        <div class="tention"><span class="iconfont icon ">&#xe645;</span></div>
        <div class="addrow">
          <el-input placeholder="" v-model=" patieninfo.idcardNo" >
            <template slot="prepend"></template>
          </el-input>
        </div>
        <div class="addrow">
          <el-input placeholder="" v-model="patieninfo.patientName" >
            <template slot="prepend"></template>
          </el-input>
        </div>
        <div class="addrow">
          <div style="display: inline-block;line-height: 40px;height: 40px;font-size: 14px;color: #909399;margin-left: 20px" ></div>
          <el-radio-group v-model="patieninfo.gender" size="mini" fill="#B78454"   style="float: right">
            <el-radio-button label="" ></el-radio-button>
            <el-radio-button label=""></el-radio-button>
          </el-radio-group>
        </div>
        <div class="addrow birthrow">
          <div style="display: inline-block;line-height: 40px;height: 40px;font-size: 14px;color: #909399;margin-left: 20px" ></div>
          <el-date-picker
            v-model="patieninfo.birthdate"
            type="date"
            placeholder=""
            format="yyyy  MM  dd "
            style="float: right;">
          </el-date-picker>
        </div>
        <div class="addrow">
          <div style="display: inline-block;line-height: 40px;height: 40px;font-size: 14px;color: #909399;margin-left: 20px" ></div>
          <el-cascader
            :options="addrees"
            clearable style="float: right;"
            separator="-"
            v-model="patieninfo.alladdrees"
          ></el-cascader>
        </div>
        <div class="addrow marriyrow">
          <div style="display: inline-block;line-height: 40px;height: 40px;font-size: 14px;color: #909399;margin-left: 20px" ></div>
          <el-cascader
            :options="marriy"
            v-model="patieninfo.maritalStatus"
            clearable style="float: right;"></el-cascader>
        </div>
        <div class="addrow" style="margin-bottom: 5px">
          <el-input placeholder="" v-model="patieninfo.phone" >
            <template slot="prepend"></template>
          </el-input>
        </div>
        <div  class="relationrow">
          <div style="line-height: 26px;height: 26px;font-size: 14px;color: #909399;margin-left: 20px" ></div>
          <el-radio-group v-model="patieninfo.relationship" size="mini" fill="#B78454" class="relationchosee"  >
            <el-radio-button label="" ></el-radio-button>
            <el-radio-button label=""></el-radio-button>
            <el-radio-button label=""></el-radio-button>
            <el-radio-button label=""></el-radio-button>
          </el-radio-group>
        </div>
        <div style="height: 50px;line-height: 50px;background: white;border-bottom:1px solid #F2F2F2 ">
          <span style="margin-left: 20px;font-size: 14px;"></span>
          <input type="radio" value="0"  v-model="patieninfo.defaultPatient" @click="isSendMessage" style="float: right;margin-right: 20px;margin-top: 18px">
        </div>
          <button type="submit" class="addpatien" @click="addpatien"></button>

      </div>
</template>

<script>
import axios from 'axios'
import moment from 'moment';
import {mapGetters} from "vuex";
export default {
  name: "AddPatien",
  data() {
    return{
       patieninfo:{
              patientName:'',
              idcardNo:'',
              phone:'',
              gender:'',
              relationship:'',
              birthdate:'',
              defaultPatient:'1',
              maritalStatus:'',
              alladdrees:''},
      num:0,
      marriy:[
        {
          value:'',
          label:''
        },
        {
          value: '',
          label: ''
        }
      ],
      addrees: [{
        value: '',
        label: '',
        children: [{
          value: '',
          label: '',
          children: [
            { value: '', label: '' },
            { value: '', label: '' },
            { value: '', label: '' }
          ]
        }, {
          value: '',
          label: '',
          children: [
            { value: '' , label: '' },
            { value: '', label: '' },
            { value: '', label: '' }
          ]
        }, {
          value: '',
          label: '',
          children: [
            { value: '', label: '' },
            { value: '', label: '' },
            { value: '', label: '' }
          ]
        }]
      }, {
        value: '',
        label: '',
        children: [{
          value:  '',
          label: '',
          children: [
            { value: '', label: '' },
            { value: '', label: '' }
          ]
        }, {
          value: '',
          label: '',
          children: [
            { value: '' , label: '' },
            { value: '' , label: '' }
          ]
        }]
      }]
    }
  },
  methods:{
    isSendMessage() {
      // let num = 0
      this.num++
/*      console.log(this.num)*/
      if(this.num % 2) {
        this.patieninfo.defaultPatient = '1'
      }else {
        this.patieninfo.defaultPatient = '0'
      }
     console.log(  this.patieninfo.defaultPatient )
    },
    addpatien() {
      let this_=this
      axios.post( `/api/user/addpatient?patientName=${this.patieninfo.patientName.trim()}&idcardNo=${this.patieninfo.idcardNo.trim()}&phone=${this.patieninfo.phone.trim()}&gender=${this.patieninfo.gender}&relationship=${this.patieninfo.relationship}&birthdate=${moment(this.patieninfo.birthdate).format('l')}&defaultPatient=${this.patieninfo.defaultPatient}&maritalStatus=${this.patieninfo.maritalStatus}&adressPro=${this.patieninfo.alladdrees[0]}&adressCountry=${this.patieninfo.alladdrees[1]}&adressCity=${this.patieninfo.alladdrees[2]}&userCode=${this.getLogUser.itemcode}&creater=${this.getLogUser.username}`)
        .then(function (response) {
          if (response.data.data == 1) {
            this_.$toast("")
          }
          else {this_.$toast('')}
        })
        .catch(function (error) {
          console.log(error);
        });
      console.log('xxxx')
    }
  },
  computed: {
    //  Vuex  getter  scenicSpotDetail 
    ...mapGetters(['getLogUser'])
  }

  }
</script>

<style scoped>
.addpatienbody{
  background: #F2F2F2;
  color: #666666;
  width: 100%;
}
.youjiantou{
  font-size: 18px;
  padding-top: 5px;
  color: black;
  font-weight: bold;
}

.tention{
  height: 40px;
  line-height: 40px;
  margin-left: 5%;
  font-weight: 400;
  font-style: normal;
  color: #999999;
  font-size: 14px;
}
.tention>span{
  color: #7BA4FC;
  font-size: 16px;
  margin-right: 1%;
}
.addrow{
  border-bottom: 1px solid #F2F2F2;
  height: 41px;
  background: white;
}
/deep/ .el-input-group__prepend{
  border: none;
  background: white;
}
/deep/.el-input-group--prepend .el-input__inner, .el-input-group__append{
  border: none;
  text-align: right;
}
/deep/ .el-radio-button__inner{
  margin-left: 10px;
  background: #F7F7F7;
}
/deep/ .el-radio-button .el-radio-button__inner{
  border-radius: 10px;
  height: 19px;
}
/deep/ .el-radio-button--mini .el-radio-button__inner{
  padding: 3px 13px;
}
/deep/ .addrow>.el-radio-group{
  height: 40px;
  line-height: 40px;
  margin-right: 10px;
}
/deep/  .el-input__inner{
  border: none;
  text-align: center;
}
/deep/ .relationrow>.el-radio-group{
      margin-left: 10px;
      height: 40px;
      line-height: 40px;
}
.relationrow{
  margin-bottom: 5px;
  height: 81px;
  background: white;
  border-bottom: 1px solid #F2F2F2;
}
/deep/ .relationchosee> .el-radio-button .el-radio-button__inner{
  margin-right: 15px;
  margin-left: 0px;
}
/deep/.el-input-group>.el-input__inner{
  width: 95%;
}
/deep/ .birthrow>.el-date-editor.el-input, .el-date-editor.el-input__inner {
  width: 190px
}
/deep/ .el-cascader{
  width: 190px;
}
.addpatien{
   color: white;
   background: #D40000;
   text-align: center;
   height: 45px;
   width: 80%;
   margin-left: 10%;
   line-height: 45px;
   margin-top: 100px;
   border-radius: 60px;
   font-size: 16px;
  border: none;
 }
</style>



