<template>
  <div class="AppointmentPatientInfo">
    <div class="allDiv">
      <!--  -->
      <div class="appointmentTime">
        <span class="appointmentTimeTitle"></span>
        <span class="appointmentTimeContent">{{ this.chooseDate.year }}-{{ this.chooseDate.month }}-{{ this.chooseDate.date }}&nbsp;{{ this.chooseDate.weekDay }}&nbsp;{{ this.timeScope }}</span>
      </div>
      <!--  -->
      <div class="appointmentPatient">
        <span class="appointmentPatientTitle"></span>
        <router-link to="/appointmentSelectPatient">
          <span class="appointmentPatientContent">
            <span v-if="appointmentChoosePatientCode==-1"></span>
            <span v-if="appointmentChoosePatientCode!=-1">{{ appointmentChoosePatient.patientName }}</span>
            <span class="appointmentPatientArrow iconfont icon-arrow-right-bold"></span>
          </span>
        </router-link>

      </div>
      <!--  -->
      <div class="appointmentIDCard">
        <span class="appointmentIDCardTitle"></span>
        <span v-if="appointmentChoosePatientCode!=-1" class="appointmentIDCardContent">{{ appointmentChoosePatient.idcardNo }}</span>
        <span v-if="appointmentChoosePatientCode==-1" class="appointmentIDCardContent"></span>
      </div>
      <!--  -->
      <div class="appointmentTel">
        <span class="appointmentTelTitle"></span>
        <input type="text" class="appointmentTelContent" placeholder="" v-model="tel" v-on:input="inputTel" />
      </div>

      <!--  -->
      <router-link tag="div" to="/appointmentSuccess" class="confirmButtonDiv">
        <span class="confirmButton"></span>
      </router-link>

      <!-- div -->
      <div class="fixDiv"></div>
    </div>
  </div>
</template>

<script>
import { mapGetters,mapMutations } from 'vuex'
export default {
  name: "AppointmentPatientInfo",
  data() {
    return {
      tel: '',
    }
  },
  methods: {
    ...mapMutations(['mTimeScope','mAppointmentUserCode','mAppointmentPatientTel']),
    inputTel(){
      this.mAppointmentPatientTel(this.tel);
    }
  },
  computed: {
    ...mapGetters(['chooseDate','timeScope','appointmentChoosePatientCode','appointmentChoosePatient','getLogUser']),
  },
  created() {
    let userCode = this.getLogUser.itemcode;
    console.log(userCode);
    this.mAppointmentUserCode(userCode);
    this.mTimeScope(this.$route.params.timeScope);
  }
}
</script>

<style lang="stylus" scoped>
.AppointmentPatientInfo
  .allDiv
    margin-top 30px
    background-color rgb(242,242,242)
    .appointmentTime
      padding-top 15px
      padding-bottom 15px
      background-color white
      border-bottom 2px solid rgb(228,228,228)
      .appointmentTimeTitle
        margin-left 20px
        font-size 16px
        color rgb(102,102,102)
        font-family ''
      .appointmentTimeContent
        font-size 16px
        font-family ''
        color rgb(102,102,102)
        margin-right 25px
        float right
    .appointmentPatient
      padding-top 15px
      padding-bottom 15px
      background-color white
      border-bottom 2px solid rgb(228,228,228)
      .appointmentPatientTitle
        margin-left 20px
        font-size 16px
        color rgb(102,102,102)
        font-family ''
      .appointmentPatientContent
        font-size 16px
        font-family ''
        color rgb(102,102,102)
        margin-right 5px
        float right
        .appointmentPatientArrow
          color rgb(119,86,55)
    .appointmentIDCard
      padding-top 15px
      padding-bottom 15px
      background-color white
      border-bottom 2px solid rgb(228,228,228)
      .appointmentIDCardTitle
        margin-left 20px
        font-size 16px
        color rgb(102,102,102)
        font-family ''
      .appointmentIDCardContent
        font-size 16px
        font-family ''
        color rgb(102,102,102)
        margin-right 25px
        float right
    .appointmentTel
      padding-top 15px
      padding-bottom 15px
      background-color white
      border-bottom 2px solid rgb(228,228,228)
      .appointmentTelTitle
        margin-left 20px
        font-size 16px
        color rgb(102,102,102)
        font-family ''
      .appointmentTelContent
        text-align right
        border none
        font-size 16px
        font-family ''
        color rgb(102,102,102)
        margin-right 25px
        float right
    .confirmButtonDiv
      display flex
      margin 70px 20px 0 20px
      .confirmButton
        font-family ''
        width 100%
        color white
        border-radius 50px
        height 35px
        padding-top 9px
        font-size 18px
        background-color rgb(212,0,0)
        text-align center
    .fixDiv
      height 100%
      background-color rgb(242,242,242)

</style>



