<template>
  <div class="AppointmentDatePicker">
    <div class="allDiv">
      <!-- Div -->
      <div class="remindDiv"></div>
      <!-- Div -->
      <div class="remindFont">
        <span class="remindFontDetail"></span>
      </div>
      <!-- Div -->
      <div class="todayDiv">
        <span class="todayDetail">{{ today }}&nbsp;{{ week }}</span>
      </div>
      <!-- datePicker -->
      <div class="datePickerDiv">
        <!--
          
          swipeSpace:
          sundayText:/...
          choosedDate:
          choosedItemColor:
          minDate:
          maxDate:
          showBorderTop:
          resizeable:
          v-on:firstDayChange:'firstDayChange'()
          v-on:change:
         -->
        <vue-horizontal-calendar
          class="datepicker"
          swipeSpace="2"
          sundayText=""
          :choosedDate="getThisMondayDate"
          choosedItemColor="rgb(119, 86, 55)"
          :minDate="minDate"
          :showBorderTop="true"
          :resizeable="false"
          v-on:firstDayChange="firstDayChange"
          v-on:change="selectDate"
          v-on:swipeToEnd="swipeToEnd"
        >
          <span class="iconfont icon-arrow-left-bold" slot="leftIcon" style="width:20px;margin-top:11px;color: rgb(119, 86, 55);font-weight: bold"></span>
          <span class="iconfont icon-arrow-right-bold" slot="rightIcon" style="width:20px;margin-top:11px;color: rgb(119, 86, 55);font-weight: bold"></span>
        </vue-horizontal-calendar>
      </div>
      <!-- Div -->
      <div class="remindDiv2"></div>
    </div>
  </div>
</template>

<script>
import VueHorizontalCalendar from 'vue-horizontal-calendar'
import {mapActions, mapMutations} from "vuex";

export default {
  name: "AppointmentDatePicker",
  components:{
    VueHorizontalCalendar
  },
  data() {
    return {
      today: '',
      week: '',
      minDate: '',
      currentFirstDay: {
        dateFormat: "",
        year: "",
        month: "",
        date: "",
        day: "",
        timestamp: ""
      },
    }
  },
  methods: {
    ...mapMutations(['mChooseDateYear','mChooseDateMonth','mChooseDateDate','mChooseWeekDay']),
    ...mapActions(['getWorkDoctorListByHospital']),
    //VueHorizontalCalendar
    getToday() {
      let date = new Date();
      let d = date.getDay();
      let day = '';
      if (d == 0) {
        day = '';
      } else if (d == 1) {
        day = '';
      } else if (d == 2) {
        day = '';
      } else if (d == 3) {
        day = '';
      } else if (d == 4) {
        day = '';
      } else if (d == 5) {
        day = '';
      } else if (d == 6) {
        day = '';
      }
      let str =  date.getFullYear() + '-' + (date.getMonth() + 1) + '-' + date.getDate();
      this.today = str;
      str = '' + day;
      this.week = str;
      str = date.getFullYear() + '/' + (date.getMonth() + 1) + '/' + date.getDate();
      this.minDate = str;
    },
    // VueHorizontalCalendar
    firstDayChange(day) {
      this.currentFirstDay = day;
    },
    // VueHorizontalCalendar
    selectDate(day){
      //
      this.mChooseDateYear(day.year);
      //
      let month = '';
      let ri = '';
      if(day.month < 10)
        month = '0' + day.month;
      else
        month = day.month;
      if(day.date < 10)
        ri = '0' + day.date;
      else
        ri = day.date;
      this.mChooseDateMonth(month);
      this.mChooseDateDate(ri);
      //
      this.mChooseWeekDay('' + day.day);
      //itemId
      let itemCode = this.$route.params.hospitalItemCode;
      //  
      this.getWorkDoctorListByHospital(itemCode);
    },
    // VueHorizontalCalendar
    swipeToEnd(){
      swal({
        title: "",
        text: "",
        icon: "error",
        button: "",
      });
    }
  },
  created() {
    this.getToday();
  },
  computed: {
    // 
    getThisMondayDate(){
      let today = new Date();
      let today_weekCode = today.getDay() == 0? 7: today.getDay();
      let monday_timestamp = today.getTime() - (today_weekCode - 1) * 1000*3600*24;
      let monday = new Date(monday_timestamp);
      // return monday.getFullYear() + "/" + (monday.getMonth() + 1) + "/" + monday.getDate();
      return today.getFullYear() + "/" + (today.getMonth() + 1) + "/" + today.getDate();
    }
  },
}
</script>

<style lang="stylus" scoped>
.AppointmentDatePicker
  .allDiv
    background white
    .remindDiv
      height 10px
      background RGB(245,245,245)
    .remindFont
      margin 10px 0 0 10px
      .remindFontDetail
        font-size 14px
        color RGB(102,102,102)
        font-family ''
    .todayDiv
      margin auto
      text-align center
      .todayDetail
        font-size 14px
        font-weight bold
        color RGB(85,85,85)
        font-family ''
    .datePickerDiv
      margin-top 10px
    .datepicker
      width 360px
      margin 0 auto
      margin-top 20px
    .remindDiv2
      height 30px
      background white
</style>



