import axios from "axios";
import {dateFormet} from "../../util/DateFormatUtil"
const state = {
  isSuccess:false,
  hospitalList: {},//
  gender:"",
  birth:"",//
  discomfortSite:"",//
  symptom:"",//
  describe:"",//

}

const actions = {
  /**/
  getHospitalList({commit},position) {
    console.dir(":"+position.city+" "+position.province)
    var path='';
    if(position!=null){
     path=`/api/natmehaHospital/selectNearBy?hospitalCity=${position.city}&hospitalPro=${position.province}`;
    }else {
      path=`/api/natmehaHospital/selectNearBy`;
    }
    axios.get(path)
      .then(response => {
        const data = response.data.data
        console.log(""+data)
        commit('storeHospitalList', data)
      })
      .catch(err => {
        throw new Error(err)
      })
  },
  /**/
  insertGuide({commit}) {
    console.log(""+state.birth+"  "+state.gender+""+state.discomfortSite+""+state.symptom+""+state.describe)
    axios.post(`/api/natmehaInteTreat/insert?gender=${state.gender}&birth=${dateFormet(state.birth)}&discomfortSite=${state.discomfortSite}&symptom=${state.symptom}&describe=${state.describe}`)
      .then(response => {
      if(response.data.data==='1'){
        console.log("")
      }else{
        console.log("")
      }
      })
      .catch(err => {
        throw new Error(err)
      });
  },

}

//
const mutations = {
  storeHospitalList(state, data) {
    state.hospitalList = data
  },

  setGender(state,gender){
    state.gender = gender
  },
  setBirthday(state,birth){
    state.birth = birth
  },
  //
  setSymptom(state,symptom){
    state.symptom = symptom
  },
  //
  setDiscomfortSite(state,discomfortSite){
  state.discomfortSite = discomfortSite
},
  //
  setDescribe(state,describe){
    state.describe = describe
  },
}

const getters = {
  hospitalList: (state) => {
    return state.hospitalList
  },


}


export default {
  state,
  actions,
  mutations,
  getters,
}



