<template>
    <div class="bar">
      <el-steps :active="active" finish-status="success" class="bar-content">
        <el-step  class="bar_content_step" :title="stepOfState1"></el-step>
        <el-step  class="bar_content_step" :title="stepOfState2"></el-step>
        <el-step  class="bar_content_step" :title="stepOfState3"></el-step>
      </el-steps>
    </div>
</template>
<script>
  export default {
    name: "IntelligentTreatProgressBar",
    props:{
      active:Number,
      stepOfState1:String,
      stepOfState2:String,
      stepOfState3:String,
    }
  }
</script>

<style lang="stylus" scoped>
.bar
  padding-top:20px;
  font-size: 15px;
  font-family: "";
  width: 70%;
  margin-left: 15%;
  .bar-content
    font-size: 14px;
    .bar_content_step/**/
      font-family: '';
      font-size:14px;
     .bar_content_step >>>.el-step__title.is-process /**/
        font-weight: 520;
        font-family: '';
        color: rgba(119,86,55,1);
     .bar_content_step >>>.el-step__head.is-process /**/
       color: rgba(119,86,55,1);
       border-color:rgba(119,86,55,1);
     .bar_content_step >>>.el-step__title.is-success/**/
       color:rgba(119,86,55,1);
       font-family: '';
     .bar_content_step >>>.el-step__icon-inner.is-status/**/
       color:rgba(119,86,55,1);
     .bar_content_step >>>.el-step__head.is-success/**/
       color: rgba(119,86,55,1);
       border-color:rgba(119,86,55,1);
       border-top-color: rgba(119,86,55,1);
       border-right-color:rgba(119,86,55,1);
       border-bottom-color: rgba(119,86,55,1);
       border-left-color: rgba(119,86,55,1);
</style>



