<template>
  <div id="healthManage">
    <div style="overflow:hidden;">
      <div class="header">
        <div class="header-left">
          <button class="btn-fanhui" @click="$router.back()">
            <div class="back-icon iconfont icon-fanhui"></div>
          </button>
        </div>
        <div class="header-title">
          <p>
            <span></span>
          </p>
        </div>
      </div>
    </div>

    <div class="pagebody">
      <div class="top">
        <div class="top-left">
          <div class="title">{{ title }}</div>
          <div class="author">{{author}}</div>
          <div class="date">{{date}}</div>
        </div>
        <div v-if="false" class="top-right">
            <div class="shoucang iconfont icon-collec_selected" v-if="!isCollect" @click="collect"></div>
            <div class="shoucang iconfont icon-shoucang" v-if="isCollect" @click="cancelCollect"></div>
        </div>
      </div>
      <div class="middle">
        <div class="content">
          <p v-html="content"></p>
        </div>
      </div>
    </div>

    <div class="bottom">
      <div class="text">
        <p></p>
      </div>
      <div class="fujian">
        <p class="p"><a class="a" href="../../../static/.pdf" download=".pdf">1.</a></p>
        <p class="p"><a class="a" href="../../../static/.doc" download=".doc">2.</a></p>
      </div>
    </div>
  </div>
</template>

<script>
    export default {
        name: "HealthManage",
      data() {
          return {
            title:'',
            author:'',
            date:'2020-09-17     110217',
            isCollect: false,
            content:'<p><span></span></p>' +
              '<p><span></span></p>' +
              '<p><span> 65 </span></p>' +
              '<p><span></span></p>' +
              '<p><span> 65  1 </span></p>' +
              '<p><span></span></p>' +
              '<p><span> 33 </span></p>' +
              '<p><span></span></p>' +
              '<p><span></span></p>' +
              '<p><span></span></p>' +
              '<p><span></span></p>' +
              '<p><span></span></p>' +
              '<p><span></span></p>' +
              '<p><span></span></p>' +
              '<p><span></span></p>' +
              '<p><span></span></p>' +
              '<p><span></span></p>' +
              '<p><span> 65 / 65 100</span></p>' +
              '<p><span></span></p>'
          }
      },

      props: {

      },
      methods: {
        back(){
          this.$router.go(-1)
        },
        collect(){
          this.isCollect = true
        },
        cancelCollect(){
          this.isCollect = false
        }
      },
      beforeRouteEnter(to, from, next) {
        //  margin:0;padding:0vue
        document.querySelector('body').setAttribute('style', 'margin:0;padding:0')
        next()
      },
      beforeRouteLeave(to, from, next) {
        // 
        document.querySelector('body').setAttribute('style', '')
        next()
      },
    }
</script>

<style lang="stylus" scoped>
  .header
    position fixed
    display: flex
    line-height: 20px
    width:100%
    background: #ffffff
    color: #000000
    .header-left
      float: left
      width: 20%
      height 16px
      text-align center
      padding-left 1px
      padding-right 1px
      .btn-fanhui
        border: none
        width: 100%
        height: 100%
        background-color: #ffffff
        .back-icon
          margin-top 14px
          color:#000000
          text-align center
          font-size 14px
          font-weight bold
    .header-title
      margin-left 20%
      text-align center
      font-size 14px

  .pagebody
    margin 60px 10px 0 10px
    .top
      margin-top 20px
      .top-left
        float left
        width 70%
        height 90px
        .title
          font-size 18px
          font-weight bold
          text-align: left
        .author
          margin-top 5px
          font-size 14px
          font-weight 400
          text-align: left
        .date
          margin-top 5px
          font-size: 14px
          font-weight 400
          text-align: left
      .top-right
        line-height 90px
        width 30%
        float right
        .shoucang
          text-align center
          font-size 30px
          color #ee9900
    .middle
      word-wrap: break-word
      color: #333333
      width 100%
      display: flex
      line-height: 24px
      .content
        color #333333
        align-self: flex-start
        display block
        letter-spacing normal
        width 100%
        font-size 14px
        text-align left
  .bottom
    margin 10px 10px 0 10px
    text-align left
    line-height 16px
    .text
      color #333333
      font-size 14px
    .fujian
      font-size 14px
      .p
        .a
          text-decoration none
</style>



